



























































































































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import Stairs from "../components/stairs.vue";
import Problem from "../components/problems.vue";
@Component({
  components: {
    Stairs,
    Problem,
  },
})
export default class One extends Vue {
  @Prop()
  clickToNext!: (c: number, p: number, url: string) => void;
  pro = "";
  isNew!: boolean;
  isPlay = false;
  musicToggle() {
    const music = document.getElementById("music") as HTMLVideoElement;
    if (music) {
      if (this.isPlay) {
        music.pause();
      } else {
        music.play();
      }
      this.isPlay = !this.isPlay;
    }
  }
  created() {
    this.isNew = this.$store.state.isNew;
    this.pro = this.isNew
      ? "初到华中大，你最想打卡哪里"
      : "刚开学课程不是很多，在一个无所事事的晚上，你最想做什么呢？";
  }
}
